import React from 'react';
import "./EnterEmail.scss"

const EnterEmail = ({onChange, value}) =>
    <div id="EnterEmail">
        <div className="content">
            <h3 className="subscribeText" ling-translate="EmailSubscribe">Subscribe to get alerts on international news, visa updates, discounts and
                more!</h3>
            <div className="inputContainer">
                <input name="email" type="email" onChange={onChange} value={value} id="input"
                       placeholder="Enter your Email"/>
                <button disabled={value === ""} className="emailButton" ling-translate="EmailSubscribeButton">Subscribe</button>
            </div>
        </div>
    </div>;

export default EnterEmail;